@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.all-filters-modal-wrapper {
  padding: 30px;
  border-radius: 20px;

  h5 {
    margin: 0;
    font-weight: 500;
  }

  .form-category {
    row-gap: 8px;
    column-gap: 30px;
    width: 100%;

    &:not(:first-child) {
      margin-top: 30px;
    }

    .form-category-row {
      display: flex;
      align-items: center;
      row-gap: inherit;
      column-gap: inherit;

      &:not(:first-child) {
        margin-top: 12px;
      }
    }
  }

  .filters-content {
    .filter-row {
      display: flex;
      align-items: center;
      margin: 12px 0;
      gap: 16px;

      .filter-lists {
        user-select: none;
        display: flex;
        align-items: start;
        gap: 60px;

        .list-wrapper {
          &:last-child .icv-list {
            max-height: 200px;
            overflow: hidden;
            overflow-y: scroll;
          }

          .list-item {
            color: pick-color('primary', 'ash');
            font-size: 14px;
            cursor: pointer;
            display: flex;
            align-items: center;
            min-width: 120px;

            mat-icon {
              font-size: 16px;
              width: 16px;
              height: 16px;
              margin-left: 6px;
            }

            &:hover {
              color: pick-color('primary', 'charcoal');
            }

            &.selected {
              color: pick-color('primary', 'blue');
            }
          }

          &.disabled {
            pointer-events: none;
            opacity: 0.5;

            .list-item {
              color: color.adjust(pick-color('primary', 'ash'), $lightness: 10%);
              cursor: not-allowed;

              mat-icon,
              &.selected {
                color: color.adjust(pick-color('primary', 'blue'), $lightness: 35%);
              }
            }
          }
        }
      }
    }

    .cta-wrapper {
      button {
        padding-inline: 32px;
      }

      .save-search-btn {
        display: flex;
        align-items: center;
        border: 2px solid pick-color('secondary', 'blue');
        color: pick-color('secondary', 'blue');
        padding: 0;

        span {
          font-size: 14px;
          line-height: 20px;
          padding: 8px 16px;
          transition: all 0.2s;

          &:hover {
            background-color: pick-color('secondary', 'blue');
            color: white;
          }
        }

        .dropdown-toggle-btn {
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 8px 10px;
          border-left: 2px solid pick-color('secondary', 'blue');
          transition: all 0.2s;

          &:hover {
            background-color: pick-color('secondary', 'blue');
            color: white;
          }

          mat-icon {
            width: 20px;
            height: 20px;
            font-size: 20px;
          }
        }
      }

      .reset-btn {
        padding: 0;
        cursor: pointer;
        @include circles(40px);
        background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 30%);
        border: none;

        svg {
          width: 20px;
          height: 20px;
          fill: pick-color('secondary', 'blue');
        }
      }
    }
  }
}
